import React, { useEffect, useState } from "react";
import "react-loader-spinner/dist/loader/css/react-spinner-loader.css";
import Loader from "react-loader-spinner";
import "./AnalysisHistory.css";
import Modal from "react-modal";
import { Link } from "react-router-dom";
import { IoMdAnalytics } from "react-icons/io";
import { IconContext } from "react-icons/lib";
import AnaData from "./analysisData";
import { useDispatch, useSelector } from "react-redux";
import axios from "axios";
import { getAnalysisHistory, postVideo } from "../../actions/analysis";
import { getAnalysis } from "../../actions/analysis";
import { CSVLink } from "react-csv";
import sumArray from "../VideoAnalysis/TeamMetrics/io";

function AnalysisHistory() {
  const [loading, setloading] = useState(false);
  Modal.setAppElement("#root");
  const dispatch = useDispatch();
  const [Finished, setFinished] = useState(false);
  const [ModalState, setModalState] = useState(false);
  const [Loading, setLoading] = useState(false);
  const modalHandler = () => setModalState(true);
  const [Mfile, setMfile] = useState(null);
  const [Imgprv, setImgprv] = useState(null);
  const { token, user } = useSelector((state) => state.auth);
  const { uploadHistory, statusText, modelData, status } = useSelector(
    (state) => state.analysis
  );
  const userid = user._id;
  const fileSelectHandler = (e) => {
    const reader = new FileReader();
    reader.onload = () => {
      if (reader.readyState === 2) setImgprv(reader.result);
    };
    reader.readAsDataURL(e.target.files[0]);
    setMfile(e.target.files[0]);
    console.log(e.target.files);
  };
  useEffect(() => {
    const load = async () => {
    if(!uploadHistory.length){
      setloading(true);
      await dispatch(getAnalysisHistory(userid));
      setloading(false);
      }
    };
    load();

     console.log('ffdfgdg');
  }, [dispatch]);

  const closeModal = () => {
    setFinished((prev) => !prev);
    setLoading(false);

    setModalState(false);
    setMfile(null);
    console.log({ Finished });
  };
  const analyzeHandler = () => {
    setFinished(false);
    setLoading(true);
    console.log(Mfile);
    dispatch(postVideo(Mfile, token));
    setInterval(() => {
      setFinished(true);
    }, 5000);
  };
  const columns = AnaData[0] && Object.keys(AnaData[0]);

  return (
    <IconContext.Provider
      value={{ color: "#fff", backgroundColor: "white", marginRight: "4px" }}
    >
      <div className="ahcontainer">
        <h1 className="ahtitle">Recent analysis history</h1>
        {/* <div className="tbcontainer"> */}
        <div className="ahbutton" onClick={modalHandler}>
          Analyze Video
        </div>
        <Modal
          isOpen={ModalState}
          onRequestClose={closeModal}
          closeTimeoutMS={500}
          style={{
            overlay: {
              position: "fixed",
              top: 0,
              left: 0,
              right: 0,
              bottom: 0,
              backgroundColor: "rgb(0, 0, 0, 0.4)",
            },
            content: {
              position: "fixed",
              background: " #FFFFFF",
              borderRadius: "19px",
              width: "580px",
              height: "250px",
              // height: Finished ? "160px" : "250px",
              /* color: #000; */
              top: "50%",
              left: "50%",
              marginTop: "0px",
              // padding: "80px 90px",
              position: "absolute",
              transform: "translate(-50%, -70%)",
              marginTop: "70px",
              padding: "30px",
            },
          }}
        >
          <div className="mdalcont">
            <h3 className="mheading">Analyse Video</h3>
            {!Loading ? (
              <div>
                <p className="mtext">
                  Fill in the following fields and your video will be processed
                  and the data extracted
                </p>
                <div className="mform">
                  <div>
                    <label className="label" htmlFor="">
                      Video
                    </label>
                    <div className="file-input">
                      <input
                        type="file"
                        id="file"
                        className="minput"
                        onChange={fileSelectHandler}
                      />
                      <label htmlFor="file">
                        <div className="upv">
                          <div className="uploadvid">Upload Video</div>
                          <div className="browz">Browse</div>
                        </div>
                      </label>
                    </div>
                  </div>
                  <div className="filp">
                    {/* <h1 className="flname"> {Mfile?.name} </h1> */}
                    {Imgprv && (
                      <video
                        className="imgprv"
                        src={Imgprv}
                        alt=""
                        style={{ height: "35px", width: "110px" }}
                      />
                    )}
                  </div>
                </div>

                <div className="mhbutton" onClick={analyzeHandler}>
                  <IoMdAnalytics />
                  Analyze Video
                </div>
              </div>
            ) : Finished ? (
              <div>
                <p className="mtext">
                  Your video is Analyzing....
                  <Link to="/VideoAnalysis/AnalysisDetails">here</Link>
                </p>
              </div>
            ) : (
              <div>
                <p className="mtext">
                  Analysis Data is being extracted from your video, pleas don't
                  close the page
                </p>
                <div className="loding">
                  <Loader
                    type="Oval"
                    color="#811aff"
                    height={50}
                    width={50}
                    timeout={8000} //3 secs
                  />
                </div>
              </div>
            )}
          </div>
        </Modal>
        {loading ? (
          <div
            style={{
              display: "flex",
              justifyContent: "center",
              paddingTop: "80px",
            }}
          >
            <Loader
              type="Oval"
              color="#811aff"
              height={60}
              width={60}
              timeout={8000} //3 secs
            />
          </div>
        ) : (
          // <div> LOADING..</div>
          <table className="table" cellPadding={0} cellSpacing={0}>
            <thead className="tr">
              <tr>
                {/* {columns.map((heading) => ( */}
                <th  style={{ width: "70%" }}>File Name </th>
                <th style={{ width: "50%" }}>Date Uploaded </th>
                <th  style={{ width: "80%" }}>Status </th>
                {/* ))} */}
              </tr>
            </thead>
            <tbody>
              {uploadHistory.map(({ filename, createdat, statusText }) => (
                <tr className="ter" key={Math.rando}>
                  <td> {filename} </td>
                  <td style={{ width: "190px" }}>{createdat} </td>
                  <td>{statusText} </td>
                  <td>
                    <div
                      style={{
                        border: !status ? `1px solid white` : "none",
                        width: "max-content",
                        // height: "25px",
                        padding: "5px 9px",
                        borderRadius: "5px",
                      }}
                    >
                      <Link
                        style={{
                          textDecoration: "none",
                          color: "white",
                          // border: "1px solid white",
                          // padding: "5px 10px",
                        }}
                        // to="/VideoAnalysis/AnalysisDetails"
                      >
                        {!status && <h5>Download Data</h5>}
                      </Link>
                    </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        )}
        {/* </div> */}
      </div>
    </IconContext.Provider>
  );
}

export default AnalysisHistory;
