import React from "react";
import "./PlayerList.css";
import { MdEdit } from "react-icons/md";
import { GoCloudDownload } from "react-icons/go";
import PlayerListItem from "./PlayerListItem";
import { useDispatch, useSelector } from "react-redux";
import { CSVLink } from "react-csv";
import NoModel from "../NoModel";
// import { getPlayers } from "../../actions/player";
import sumArray from "../TeamMetrics/io";

function PlayerList() {
  let { players } = useSelector((state) => state.player);
  const { statusText, modelData, status } = useSelector(
    (state) => state.analysis
  );
  // players = players.slice(0, 11);
  const player1 = players.length > 0 ? players.slice(0, 11) : [];
  const player2 = players.length > 0 ? players.slice(16, 27) : [];
  if (
    // modelData &&
    modelData.length === 0
  ) {
    return <NoModel />;
  }
  if (!status) {
    return <NoModel />;
  }

  const GoalAttempts1 = sumArray(modelData?.team1_metrics?.GoalAttempts);
  const BallPossession1 = sumArray(modelData?.team1_metrics?.BallPossession);
  const NumberOfGoals1 = sumArray(modelData?.team1_metrics?.NumberOfGoals);
  const NumberOfPasses1 = sumArray(modelData?.team1_metrics?.NumberOfPasses);
  const ShortPass1 = sumArray(modelData?.team1_metrics?.ShortPass);
  const LongPass1 = sumArray(modelData?.team1_metrics?.LongPass);
  const Distance1 = sumArray(modelData?.team1_metrics?.Distance);

  const GoalAttempts3 = sumArray(modelData?.team2_metrics?.GoalAttempts);
  const BallPossession3 = sumArray(modelData?.team2_metrics?.BallPossession);
  const NumberOfGoals3 = sumArray(modelData?.team2_metrics?.NumberOfGoals);
  const NumberOfPasses3 = sumArray(modelData?.team2_metrics?.NumberOfPasses);
  const ShortPass3 = sumArray(modelData?.team2_metrics?.ShortPass);
  const LongPass3 = sumArray(modelData?.team2_metrics?.LongPass);
  const Distance3 = sumArray(modelData?.team2_metrics?.Distance);
  const arr1 = [
    modelData?.team1_metrics?.players,
    GoalAttempts1,
    BallPossession1,
    NumberOfGoals1,
    NumberOfPasses1,
    ShortPass1,
    LongPass1,
    // Distance1,
  ];
  const arr2 = [
    modelData?.team1_metrics?.players,
    GoalAttempts3,
    BallPossession3,
    NumberOfGoals3,
    NumberOfPasses3,
    ShortPass3,
    LongPass3,
    // Distance3,
  ];
  const dhata = arr1.concat(arr2);
  return (
    <div className="pldisp">
      <div className="timdld">
        <div className="iedit">
          <input className="team" placeholder="Team 1" />
          <div className="mcedi">
            <MdEdit />
          </div>
        </div>
        <div className="dndbtn">
          <GoCloudDownload />
          <CSVLink
            data={dhata}
            filename={"mye.csv"}
            className="btn"
            style={{
              textDecoration: "none",
              color: "white",
              // border: "1px solid white",
              // padding: "5px 10px",
              // fontSize: "10px",
            }}
            target="_blank"
            onClick={() => {
              console.log("You click the link"); //  Your click handling logic
            }}
          >
            Download team metric
          </CSVLink>
        </div>
      </div>

      <div className="plgridd">
        {player1.map((player) => (
          <PlayerListItem player={player} />
        ))}
      </div>
      <div className="timdld">
        <div className="iedit">
          <input className="team" placeholder="Team 2" />
          <div className="mcedi">
            <MdEdit />
          </div>
        </div>
        <div className="dndbtn">
          <GoCloudDownload />
          <CSVLink
            data={dhata}
            filename={"mye.csv"}
            className="btn"
            style={{
              textDecoration: "none",
              color: "white",
              // border: "1px solid white",
              // padding: "5px 10px",
              // fontSize: "10px",
            }}
            target="_blank"
            onClick={() => {
              console.log("You click the link"); //  Your click handling logic
            }}
          >
            Download team metric
          </CSVLink>
        </div>
      </div>

      <div className="plgridd">
        {player2.map((player) => (
          <PlayerListItem player={player} />
        ))}
      </div>
    </div>
  );
}

export default PlayerList;
