import React, { useEffect, useState } from "react";
import "./PlayerLibrary.css";
import PlayerListItem from "../VideoAnalysis/PlayerList/PlayerListItem";
import { MdArrowDropDown, MdSearch } from "react-icons/md";
import { useDispatch, useSelector } from "react-redux";
import { getPlayers, getPlayersByClub } from "../../actions/player";
import ReactPaginate from "react-paginate";
import NoModel from "../VideoAnalysis/NoModel";

function PlayerLibrary() {
  const [Search, setSearch] = useState("");
  const [Pagenumber, setPagenumber] = useState(0);

  const PlayersPerpage = 30;
  const pageVisited = PlayersPerpage * Pagenumber;
  const { modelData, status } = useSelector((state) => state.analysis);
  const { model_data } = modelData;
  const team1 = model_data?.Soccer_teams[0];
  const team2 = model_data?.Soccer_teams[1];
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(
      getPlayersByClub(
        modelData &&
          modelData?.Soccer_teams &&
          modelData?.Soccer_teams.length > 0 &&
          modelData?.Soccer_teams[0],
        modelData &&
          modelData?.Soccer_teams &&
          modelData?.Soccer_teams.length > 0 &&
          modelData?.Soccer_teams[1]
      )
    );
  }, [getPlayersByClub]);
  // if (!players) return;
  // const teamm = [team1, team2]
  // Soccer_teams
  let { players } = useSelector((state) => state.player);
  // console.log(players);
  const [Searchplayer1, setplayer1] = useState(players);
  if (!status) {
    <div className="okna">
      return <NoModel />;
    </div>;
  }
  if (
    // modelData &&
    modelData.length === 0
  ) {
    <div className="okna">
      return <NoModel />;
    </div>;
  }
  if (!players) {
    return (
      <div className="okna">
        <NoModel />
      </div>
    );
  }
  if (Object.keys(modelData).length === 0) {
    return (
      <div className="okna">
        <NoModel />
      </div>
    );
  }

  players = players.slice(0, 70);
  const pageCount = Math.ceil(players.length / PlayersPerpage);
  const filteredPlayers = players.filter((player) =>
    player.name.toLowerCase().includes(Search.toLowerCase())
  );
  const displayPlayers = filteredPlayers
    // const displayPlayers = players
    .slice(pageVisited, pageVisited + PlayersPerpage)
    .map((player) => <PlayerListItem player={player} />);
  const changePage = ({ selected }) => {
    setPagenumber(selected);
  };
  return (
    <div className="plcontainer">
      <h2 className="plheading">Player Library</h2>
      <div className="flek">
        <select placeholder=" All Teams" className="seam imu">
          <MdArrowDropDown />
          <option value="team">{model_data?.Soccer_teams[0]} </option>
          <option value="team">{model_data?.Soccer_teams[1]} </option>
        </select>
        <input
          type="text"
          className="seam"
          placeholder=" Search Player"
          onChange={(e) => setSearch(e.target.value)}
        />
        {/* Search Player <MdSearch /> */}
      </div>
      <div className="plfleks">{displayPlayers}</div>
      <ReactPaginate
        previousLabel={"Previous"}
        nextLabel={"Next"}
        pageCount={pageCount}
        onPageChange={changePage}
        containerClassName={"pagination"}
        previousLinkClassName={"previousbtn"}
        nextLinkClassName={"nextbtn"}
        activeClassName={"pagactive"}
      />
    </div>
  );
}

export default PlayerLibrary;
