import React, { useState } from "react";
import "./TeamMetrics.css";
import { Bar } from "react-chartjs-2";
import { useDispatch, useSelector } from "react-redux";
import sumArray from "./io";
import NoModel from "../NoModel";
import { CSVLink } from "react-csv";

function TeamMetrics() {
  const { modelData, status } = useSelector((state) => state.analysis);
  // const { modelData } = modelData;

  console.log("modelData", modelData);
  const GoalAttempts1 = sumArray(modelData?.team1_metrics?.GoalAttempts);
  const BallPossession1 = sumArray(modelData?.team1_metrics?.BallPossession);
  const NumberOfGoals1 = sumArray(modelData?.team1_metrics?.NumberOfGoals);
  const NumberOfPasses1 = sumArray(modelData?.team1_metrics?.NumberOfPasses);
  const ShortPass1 = sumArray(modelData?.team1_metrics?.ShortPass);
  const LongPass1 = sumArray(modelData?.team1_metrics?.LongPass);
  const Distance1 = sumArray(modelData?.team1_metrics?.Distance);

  const GoalAttempts3 = sumArray(modelData?.team2_metrics?.GoalAttempts);
  const BallPossession3 = sumArray(modelData?.team2_metrics?.BallPossession);
  const NumberOfGoals3 = sumArray(modelData?.team2_metrics?.NumberOfGoals);
  const NumberOfPasses3 = sumArray(modelData?.team2_metrics?.NumberOfPasses);
  const ShortPass3 = sumArray(modelData?.team2_metrics?.ShortPass);
  const LongPass3 = sumArray(modelData?.team2_metrics?.LongPass);
  const Distance3 = sumArray(modelData?.team2_metrics?.Distance);

  const arr1 = [
    modelData?.team1_metrics?.players,
    GoalAttempts1,
    BallPossession1,
    NumberOfGoals1,
    NumberOfPasses1,
    ShortPass1,
    LongPass1,
    // Distance1,
  ];
  const arr2 = [
    modelData?.team1_metrics?.players,
    GoalAttempts3,
    BallPossession3,
    NumberOfGoals3,
    NumberOfPasses3,
    ShortPass3,
    LongPass3,
    // Distance3,
  ];
  const dhata = arr1.concat(arr2);

  const [label1, setInputlabel1] = useState("Goals");
  const [label2, setInputlabel2] = useState("Goals");

  const [data1, setData1] = useState(NumberOfGoals1);
  const [data2, setData2] = useState(NumberOfGoals3);
  console.log("Number", NumberOfGoals1);
  const selectHandler1 = (e) => {
    setInputlabel1(e.target.value);
    if (e.target.value === "Goals") setData1(NumberOfGoals1);
    if (e.target.value === "GoalAttempts") setData1(GoalAttempts1);
    if (e.target.value === "Passescompleted") setData1(NumberOfPasses1);
    if (e.target.value === "Ballpossession") setData1(BallPossession1);
    if (e.target.value === "Shortpasses") setData1(ShortPass1);
    if (e.target.value === "Longpasses") setData1(LongPass1);
    if (e.target.value === "Distance") setData1(Distance1);
  };

  const selectHandler2 = (e) => {
    setInputlabel2(e.target.value);
    if (e.target.value === "Goals") setData2(NumberOfGoals3);
    if (e.target.value === "GoalAttempts") setData2(GoalAttempts3);
    if (e.target.value === "Passescompleted") setData2(NumberOfPasses3);
    if (e.target.value === "Ballpossession") setData2(BallPossession3);
    if (e.target.value === "Shortpasses") setData2(ShortPass3);
    if (e.target.value === "Longpasses") setData2(LongPass3);
    if (e.target.value === "Distance") setData2(Distance3);
  };
  if (
    // modelData &&
    modelData.length === 0
  ) {
    return <NoModel />;
  }
  if (!status) {
    return <NoModel />;
  }

  return (
    <div className="eventcontainer">
      <h3 className="tmtitle">OVERALL TEAM METRICS</h3>
      <div className="tmta">
        <div className="tmcont">
          {/* <div className="tmpchart"> */}
          <div className="bar">
            <div className="sele">
              <div
                className="timm1"
                style={{ backgroundColor: modelData?.team1_color }}
              >
                <h5>{modelData?.Soccer_teams[0]} </h5>
              </div>
              <div className="selct">
                <select value={label1} onChange={selectHandler1}>
                  <option value="Goals">Goals</option>
                  <option value="GoalAttempts">Goal Attempts</option>
                  <option value="Passsescompleted">Passses completed</option>
                  <option value="Ballpossession">Ball possession</option>
                  <option value="Longpasses">Long passes</option>
                  <option value="Shortpasses">Short passes</option>
                </select>
              </div>
            </div>
            <Bar
              width={20}
              height={100}
              data={{
                labels: modelData?.team1_metrics?.players,
                datasets: [
                  {
                    label: `${label1}`,
                    data: data1,
                    backgroundColor: "#A257FF",
                    borderColor: "#ffffff",
                    borderWidth: 1,
                    barThickness: 40,
                  },
                ],
              }}
              options={{
                maintainAspectRatio: false,
                legend: {
                  labels: {
                    // This more specific font property overrides the global property
                    fontColor: "white",
                    // fontSize: '1px'
                  },
                },
              }}
            />
          </div>
          {/* </div> */}
          <div className="bar">
            <div className="sele">
              <div
                className="timm1 b"
                style={{ backgroundColor: modelData?.team2_color }}
              >
                <h5>{modelData?.Soccer_teams[1]} </h5>
              </div>
              <div className="selct">
                <select value={label2} onChange={selectHandler2}>
                  <option value="Goals">Goals</option>
                  <option value="Goal Attempts">Goal Attempts</option>
                  <option value="Passses completed">Passses completed</option>
                  <option value="Ball possession">Ball possession</option>
                  <option value="Long passes">Long passes</option>
                  <option value="Short passes">Short passes</option>
                </select>
              </div>
            </div>
            <Bar
              width={20}
              height={100}
              data={{
                labels: modelData?.team2_metrics?.players,
                datasets: [
                  {
                    label: `${label2}`,
                    data: data2,
                    backgroundColor: "#A257FF",
                    borderColor: "#ffffff",
                    borderWidth: 1,
                    barThickness: 40,
                  },
                ],
              }}
              options={{
                maintainAspectRatio: false,
                responsive: true,
                legend: {
                  labels: {
                    // This more specific font property overrides the global property
                    fontColor: "white",
                    // fontSize: '1px'
                  },
                },
              }}
            />
          </div>
        </div>
        <div className="tmtable">
          <div>
            <table className="ewq">
              <thead>
                <tr>
                  <th scope="col"></th>
                  <th
                    style={{
                      // color: "#D52121",
                      fontSize: "14px",
                      fontWeight: "bold",
                    }}
                  >
                    {/* MANCHESTER UNITED */}
                    {modelData?.Soccer_teams[0]}
                  </th>
                  <th
                    style={{
                      // color: "#5597FF",
                      fontSize: "14px",
                      fontWeight: "bold",
                    }}
                  >
                    {/* CHELSEA */}
                    {modelData?.Soccer_teams[1]}
                  </th>
                </tr>
              </thead>
              <tbody>
                <tr>
                  <td>Possession (%)</td>
                  <td> {modelData?.team1_possession} </td>
                  <td> {modelData?.team2_possession} </td>
                </tr>
                <tr>
                  <td>Goals</td>
                  <td> {modelData?.team1_goals} </td>
                  <td> {modelData?.team2_goals} </td>
                </tr>
                <tr>
                  <td>Passes Completed</td>
                  <td> {modelData?.team1_passes_completed} </td>
                  <td> {modelData?.team2_passes_completed} </td>
                </tr>
                <tr>
                  <td>Shots</td>
                  <td> {modelData?.team1_shots} </td>
                  <td> {modelData?.team1_shots} </td>
                </tr>
                <tr>
                  <td>Shots on target</td>
                  <td>{modelData?.team1_shots_on_target} </td>
                  <td>{modelData?.team2_shots_on_target} </td>
                </tr>
              </tbody>
            </table>

            <CSVLink
              data={dhata}
              filename={"mye.csv"}
              className="btn btn-primary"
              target="_blank"
              onClick={() => {
                console.log("You click the link"); //  Your click handling logic
              }}
            >
              Download me
            </CSVLink>
          </div>
        </div>
      </div>
    </div>
  );
}

export default TeamMetrics;
